}

body {
  color: "#617D98";
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  min-height: 100vh;
}

.hidden {
  display: none;
}

li {
  list-style: none;
}

.welcome-content {
  width: 80%;
  margin: auto;
}

.fridge-content {
  width: 80%;
  margin: auto;
  display: flex;
}

.menu {
  width: 20%;
  min-width: 135px;
  height: 100%;
