.layout .line {
  height: .75px;
  background-color: #00008b;
  border: none;
}

.cards-list {
  width: 100%;
  grid-gap: 1.5rem;
  grid-template-columns: repeat(3, 1fr);
  padding: 2em 0;
  display: grid;
}

.card {
  width: auto;
