}

.delete-ing {
  color: #fff;
  background-color: #e11c1c;
  border: none;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  display: flex;
}

.delete-ing path {
  width: 24px;
  height: 24px;
}

